.inventory-dashboard .dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}
.inventory-dashboard .dashboard-header .header-left .dashboard-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111827;
  margin-bottom: 0.5rem;
}
.inventory-dashboard .dashboard-header .header-left .dashboard-subtitle {
  color: #6b7280;
  font-size: 1rem;
  margin: 0;
}
.inventory-dashboard .dashboard-header .header-right .utility-options {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.inventory-dashboard .dashboard-header .header-right .utility-options .auto-refresh {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #6b7280;
  font-size: 0.875rem;
}
.inventory-dashboard .dashboard-header .header-right .utility-options .auto-refresh svg {
  color: #9ca3af;
}
.inventory-dashboard .dashboard-header .header-right .utility-options .refresh-now {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #f3f4f6;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s;
}
.inventory-dashboard .dashboard-header .header-right .utility-options .refresh-now:hover {
  background-color: #e5e7eb;
  border-color: #9ca3af;
}
.inventory-dashboard .dashboard-header .header-right .utility-options .refresh-now svg {
  color: #6b7280;
}
.inventory-dashboard .dashboard-tabs {
  margin-bottom: 2rem;
}
.inventory-dashboard .dashboard-tabs .tabs-nav {
  display: flex;
  border-radius: 0.75rem;
  padding: 0.25rem;
}
.inventory-dashboard .dashboard-tabs .tabs-nav .tab-link {
  width: 100%;
  justify-content: center;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 5px 1.5rem;
  text-decoration: none;
  color: #6b7280;
  font-weight: 500;
  border-radius: 0.5rem;
  transition: all 0.2s;
  white-space: nowrap;
  cursor: pointer;
}
.inventory-dashboard .dashboard-tabs .tabs-nav .tab-link:hover {
  color: #374151;
  background-color: #f9fafb;
}
.inventory-dashboard .dashboard-tabs .tabs-nav .tab-link.active {
  background-color: #3b82f6;
  color: white;
}
.inventory-dashboard .dashboard-tabs .tabs-nav .tab-link svg {
  width: 1.25rem;
  height: 1.25rem;
}
.inventory-dashboard .dashboard-content .content-placeholder {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 2rem;
  text-align: center;
  color: #6b7280;
}
